import React from "react";
import Myfunccomponent from "./Myfunccomponent";

class Mycomponent extends React.Component {
  render() {
    return (
      <div>
        <Myfunccomponent fname={this.props.fname} lname={this.props.lname} />
        <div class="card">
          <h5 class="card-title"> First Name : {this.props.fname}</h5>
          <h5 class="card-title"> Last Name : {this.props.lname}</h5>

          <img
            class="card-img-top"
            src="data:image/jpeg;base64,"
            alt="Card image cap"
          />
          <p class="card-text">
            DotNet Tech Leader <br />
            Azure Cloud Certified Developer
          </p>
          <a href="https://www.google.com" class="btn btn-primary">
            Go Google
          </a>
        </div>
      </div>
    );
  }
}

export default Mycomponent;
