import React from "react";

function Myfunccomponent(props) {
  return (
    <div>
      <table class="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">First</th>
            <th scope="col">Last</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th scope="row">1</th>
            <td>{props.fname}</td>
            <td>{props.lname}</td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}

export default Myfunccomponent;
